export default {
	data() {
		return {
			list:[],
		};
	},
	onShow(){
		this.list=[]
		this.intData()
	},
	methods:{
		intData(){
			let self =this
			let params ={
				pageIndex: 1,
				pageSize: 100
			}
			this.$acFrame.HttpService.getToicList().then(res => {
			  if (res.success) {
			    self.list = res.data
			  }
			});
		},
		chooseIt(ind){
			let pages = getCurrentPages()
			let obj=this.list[ind]
			if(pages.length>1){
				let prePage = pages[pages.length - 2]
				if (prePage.$vm.changeData) {
				  prePage.$vm.setVal(obj.id,obj.name,'subject')
				  wx.navigateBack({})
				}
			}
		},
		detail(id){
			uni.navigateTo({
				 url: 'topicIndex?id='+id
			});
		}
	}
}
